body,
html {
  height: 100%;
  min-height: 100%;
  margin: 0;
  padding: 0;
}
hr {
  border-width: 0;
  height: 1px;
  background-color: #ddd;
  margin: 15px 0;
}
a,
a:visited,
a:hover {
  color: #1565C0;
  text-decoration: none;
}
a:hover {
  text-decoration: underline;
}
.content-padding {
  padding: 20px;
}
.content-padding-h {
  padding-left: 20px;
  padding-right: 20px;
}
.content-padding-v {
  padding-top: 20px;
  padding-bottom: 20px;
}
#app-view {
  position: relative;
  min-height: 100%;
  padding-top: 64px;
  padding-bottom: 56px;
}
#preloader {
  animation-duration: 1s;
  animation-timing-function: linear;
}
.toolbar-fixed {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 61;
}
.footer-fixed {
  color: #777;
  background-color: #fafafa;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 56px;
  z-index: 61;
}
.app-header-title {
  font-size: 150%;
  font-weight: lighter;
}
.app-header-title .icon {
  font-size: 150%;
}
.app-header-title .v75 {
  font-size: 120%;
}
.app-header-title .spacy {
  letter-spacing: 5px;
}
#logo-icon {
  animation-delay: 2s;
}
.no-margin {
  margin: 0 !important;
}
.auth-card-wrapper {
  max-width: 500px;
  margin-left: auto;
  margin-right: auto;
}
.auth-card-wrapper-sm {
  max-width: 350px;
  margin-left: auto;
  margin-right: auto;
}
.appdea-create-container {
  margin-bottom: 20px;
  margin-left: 10px;
  margin-right: 10px;
}
.appdea-creator {
  max-width: 500px;
  margin: 0 auto;
}
md-dialog {
  max-width: 600px;
  margin: 0 10px;
}
appdea-view {
  display: block;
}
md-toolbar,
footer {
  animation-duration: 1s;
  animation-delay: 1s;
}
.animate-fade.ng-enter {
  position: relative;
  animation: fadeIn 1s;
}
md-input-container.no-m {
  margin: 0;
  margin-top: 10px;
}
md-input-container.m-sm {
  margin: 10px 0;
  margin-top: 20px;
}
appdea-view [marked] {
  line-height: 150%;
}
appdea-view.ng-move,
appdea-view.ng-enter,
appdea-view.ng-leave {
  transition: all ease 0.5s;
}
appdea-view.ng-enter {
  opacity: 0;
  transform: scale(0.5) translateY(-500px);
}
appdea-view.ng-enter.ng-enter-active {
  opacity: 1;
  transform: scale(1) translateY(0);
}
appdea-view.ng-leave {
  opacity: 1;
  transform: scale(1) translateY(0);
}
appdea-view.ng-leave.ng-leave-active {
  opacity: 0;
  transform: scale(0.5) translateY(200px);
}
appdea-view md-toolbar .subtitle {
  font-size: 60%;
}
