md-sidenav {
  min-width: 250px;
  max-width: 250px;
  width: 250px;
}
md-sidenav:not(.md-locked-open) {
  padding-top: 64px;
}
.md-sidenav-menu .menu-heading {
  color: #1565C0;
}
.md-sidenav-menu .md-button.active,
.md-sidenav-menu .md-button:hover,
.md-sidenav-menu .md-button:focus {
  color: white;
  background: #1565C0;
}
@media (max-width: 599px) {
  .content-padding {
    padding-left: 0;
    padding-right: 0;
  }
  .content-padding-h {
    padding-left: 0;
    padding-right: 0;
  }
}
